@import url(https://fonts.googleapis.com/css?family=Arvo&display=swap);
/* Define global variables to be used in styles.css */
:root {
	--primaryFont: 'Arvo', serif;

	/*COLORS*/
	/*light mode*/
	--light-grayColor: #BBADA0;
	--light-primaryBackground: #FAF8EF;
	--light-primaryTextColor: #776E65; 

	--light-tileBackground: rgba(238, 228, 218, 0.35);
	--light-tileNumberColor: white;

	--light-beige: #eee4da;
	--light-beigeYellow: #ede0c8;
	--light-paleOrange: #f2b179;
	--light-brightOrange: #f59563; 
	--light-brighterOrange: #f67c5f;
	--light-brightestOrange: #f65e3b;
	--light-white: #f9f6f2;
	--light-yellow: #edcf72;

	/*dark mode*/
	--dark-primaryBackground: #0C0A2F;
	--dark-primaryTextColor: #8777b3; 
	--dark-tileBackground: #292553; 
	--dark-tileNumberColor: #424287;

	--dark-teal: #4AA6E2;
	--dark-dullBlue: #37588c;
	--dark-coolWhite: #ABDAF4;
	--dark-warmWhite: #D7D2FC;
	--dark-reddishPurple: #1B0E2D;
	--dark-blue: #102559;
	--dark-darkestBlue: #010D2B;
	--dark-pink: #b55e74;
}


html {
	/*COLORS*/
	--primaryBackground: var(--light-primaryBackground);
	--primaryTextColor: var(--light-primaryTextColor); 

	/* TEXT */ 
	--titleColor: var(--primaryTextColor);

	/* BUTTONS */
	--primaryButtonColor: var(--light-grayColor);
	--primaryButtonText: white; 
	--pauseButtonColor: #8F7A66; 
	--highlightTextColor: var(--primaryTextColor); 

	/*FORM*/
	--formInputBackground: white;
	--formInputTextColor: var(--primaryTextColor);
	--formInputFocus: lightblue; 

	/*BOARD*/
	--boardBackground: var(--light-grayColor);
	--tileBackground: var(--light-tileBackground);
	--tileNumberColor: var(--light-tileNumberColor);
}

html[data-theme='dark'] {
	/*COLORS*/
	--primaryBackground: var(--dark-primaryBackground);
	--primaryTextColor: var(--dark-primaryTextColor); 
	
	/* TEXT */ 
	--titleColor: #7c6eb7; 

	/* BUTTONS */
	--primaryButtonColor: #292553;
	--primaryButtonText: #4AA6E2;
	--pauseButtonColor: #8F7a66; 
	--highlightTextColor: #B53E75; 

	/*FORM*/
	--formInputBackground: var(--dark-warmWhite);
	--formInputTextColor: var(--primaryTextColor);
	--formInputFocus: lightblue; 

	/*BOARD*/
	--boardBackground: #1b1a3d;
	--tileBackground: var(--dark-tileBackground);
	--tileNumberColor: var(--dark-tileNumberColor);
}

body {
	/* Global variables */
	font-family: var(--primaryFont);
	background-color: var(--light-primaryBackground);
	background-color: var(--primaryBackground);
	color: var(--light-primaryTextColor);
	color: var(--primaryTextColor);
}

p {
	color: var(--light-primaryTextColor);
	color: var(--primaryTextColor);
}

h1 {
	color: var(--light-primaryTextColor);
	color: var(--titleColor);
  font-size: 48px;
}

button {
	font-family: var(--primaryFont);
	background-color: var(--light-grayColor);
	background-color: var(--primaryButtonColor);
	color: white;
	color: var(--primaryButtonText);
	border: none;
	text-align: center;
	padding: 5px;
	margin: 5px;
	radius: 2px;
}

.highlight {
	color: var(--light-primaryTextColor);
	color: var(--highlightTextColor);
}

.container {
	display: flex;
	height: 100%;
	justify-content: center;
}

.leftContainer {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}

.leftContainer > div {
	padding: 10px;
}

.rightContainer {
	padding: 10px;
}

.rightContainer > div {
	margin: 10px;
}

.header {
	width: 100%;
}

.subheader {
	font-size: 16px;
}

.footer {
	width: 100%;
	grid-column: 1 / -1;
}

.gameContainer {
  display: flex;
  flex-direction: row;
	align-items: stretch;
	grid-column: 1 / -1;
}

/*BOARD*/

.board {
	display: grid;
	align-items: stretch;
	justify-content: center;
	width: 100%;
	border-radius: 2px;
	background: var(--light-grayColor);
	background: var(--boardBackground);
	border: 5px solid var(--light-grayColor);
	border: 5px solid var(--boardBackground);
}

.status {
  margin-bottom: 10px;
}

.tile {
	background-color: var(--light-tileBackground);
	background-color: var(--tileBackground);
	border: none;
	color: var(--light-tileNumberColor);
	color: var(--tileNumberColor);
	text-align: center;
	font-family: var(--primaryFont);
  font-size: 24px;
  font-weight: bold;
	font-color: black;
	margin: 2px;
  width: 100px;
	height: 100px;
	margin: 2px;
	/*align-self: flex-end;*/
}

/*Additional components*/

.stopwatch {
	width: 100%;
}

.pauseButton {
	width: 30%;
}

form {
	display: grid;
	width: 80%;
	font-family: var(--primaryFont)
}

input {
	border-radius: 2px;
	padding: 5px;
	margin: 5px;
}
	input[type=text] {
		background-color: white;
		background-color: var(--formInputBackground);
		color: var(--light-primaryTextColor);
		color: var(--formInputTextColor);
		border: none;
		font-family: var(--primaryFont);
	}
	input[type=text]:focus {
		background-color: lightblue;
		background-color: var(--formInputFocus);
	}
	input[type=submit] {
		/*TO DO: replicates pause button, consider refactoring*/
		background-color: var(--light-grayColor);
		background-color: var(--primaryButtonColor);
		border: none;
		color: white;
		color: var(--primaryButtonText);
		text-align: center;
		font-family: var(--primaryFont);
	}

#scoresHeader {
}

html {
	/*light mode*/
	--tileBackground-2: var(--light-beige);
	--tileTextColor-2: var(--light-primaryTextColor);

	--tileBackground-4: var(--light-beigeYellow);
	--tileTextColor-4: var(--light-primaryTextColor);

	--tileBackground-8: var(--light-paleOrange);
	--tileTextColor-8: var(--light-white); 

	--tileBackground-16: var(--light-brightOrange);
	--tileTextColor-16: var(--light-white);

	--tileBackground-32: var(--light-brighterOrange);
	--tileTextColor-32: var(--light-white);

	--tileBackground-64: var(--light-brightestOrange);
	--tileTextColor-64: var(--light-white);

	--tileBackground-128: var(--light-yellow);
	--tileTextColor-128: var(--light-white);

	--tileBackground-256: var(--light-yellow);
	--tileTextColor-256: var(--light-white);

	--tileBackground-512: var(--light-yellow);
	--tileTextColor-512: var(--light-white);

	--tileBackground-1024: var(--light-yellow);
	--tileTextColor-1024: var(--light-white);

	--tileBackground-2048: var(--light-yellow);
	--tileTextColor-2048: var(--light-white);
}

html[data-theme='dark'] {
	/*light dark*/
	--tileBackground-2: #292553; 
	/*--tileTextColor-2: #534287;*/
	--tileTextColor-2: var(--dark-primaryTextColor);

	--tileBackground-4: #2e2d5e;
	--tileTextColor-4: #8777b3;

	--tileBackground-8: #353572;
	--tileTextColor-8: var(--dark-teal); 

	--tileBackground-16: var(--dark-dullBlue);
	--tileTextColor-16: var(--dark-coolWhite);

	--tileBackground-32: #1460a5;
	--tileTextColor-32: var(--dark-coolWhite);

	--tileBackground-64: #0f4a84; 
	--tileTextColor-64: var(--dark-coolWhite);

	--tileBackground-128: var(--dark-blue); 
	--tileTextColor-128: var(--dark-coolWhite);

	--tileBackground-256: var(--dark-reddishPurple); 
	--tileTextColor-256: var(---dark-warmWhite);

	--tileBackground-512: var(--dark-reddishPurple);
	--tileTextColor-512: var(---dark-warmWhite);

	--tileBackground-1024: var(--dark-reddishPurple);
	--tileTextColor-1024: var(--dark-warmWhite);

	--tileBackground-2048: var(--darkestBlue);
	--tileTextColor-2048: var(--dark-pink);
}

